// 
export interface LotteryConfig {
  id: number; // id
  name: string; // 
  is_allow_watch: boolean; // : true- false-
  join_types: LotterJoinType[]; // 
  draw_range?: number;
  status: LotteryStatus;
  is_ok: boolean; // truefalse
  prize?: LotteryPrizeItem[];
}

// 
export enum LotterJoinType {
  LocalImport = 'local_import', // 
  Phone = 'phone', // 
  Nickname = 'nickname', // 
}

// 
export interface LotteryResult {
  is_join: boolean; // 
  is_allow_watch: boolean; // 
  is_ok: boolean;
  prize: LotteryPrizeResult[];
}

// 
export interface LotteryPrizeItem {
  award_name: string; // 
  count: number; // 
  prize_id: number; // id
  image: string; // 
  name: string; // 
  open_at: number; // 
  open_type: LotteryPrizeOpenType; //  1  0 
  start_at: number; // 
  status: LotteryPrizeStatus; //  0  1  2  100 
}

// 
export interface LotteryPrizeResult {
  prize_id: number; // id
  is_lucky: boolean; // 
  people: PrizeResultPeopleItem[]; // 
}

// 
export interface PrizeResultPeopleItem {
  viewer_name: string; // 
  viewer_message: string; // 
}

// 
export enum LotteryStatus {
  Close = 0, // 
  Open = 1, // 
  Unknow = 100, // 
}

// 
export enum LotteryPrizeOpenType {
  Timing = 0, // 
  Manual = 1, // 
}

// 
export enum LotteryPrizeStatus {
  NotLotteryYet = 0, // 
  Drawing = 1, // 
  Completed = 100, // 
}

export interface WsLotteryPrizeItem {
  award_name: string;
  count: number;
  id: number;
  img: string;
  lottery_id: number;
  name: string;
  open_at: string;
  open_type: number;
  sort: number;
  start_at: string;
  status: number;
}

export interface WsLottery {
  event: WsLotteryEvents;
  data: WsLotteryConfig | WsLotteryPrizeItem;
}
export interface WsLotteryConfig {
  created_at: string;
  draw_range: number;
  id: number;
  is_allow_viewer_watch: number;
  name: string;
  status: number;
}

export enum WsLotteryEvents {
  LotteryOpen = 'lottery_open',
  PrizeOpen = 'prize_open',
  PrizeOver = 'prize_over',
  ViewerJoin = 'viewer_join',
}
