import { http } from '@core/http';
import { stringify } from 'qs';
import {
  AdvertisementConfig,
  AttachmentConfig,
  BasicConfig,
  DecorationConfig,
  MediaHubConfig,
  PlaybackConfig,
  SocialShareConfig,
  WechatJssdkConfig,
  NumberResult,
  UPV,
} from '@type/basis.type';
import { Observable } from 'rxjs';
import { ApiPrefix } from 'src/constans/base';
import { ApiHost } from './host';

/**
 * @description: 
 * @param {data: { act_id: string }} act_id HashID
 * @return {*}
 */
export const getBasicConfig = (data: { act_id: string }): Observable<BasicConfig> =>
  http.get(`${ApiHost}${ApiPrefix}/activities/${data.act_id}/configs/basic`);

